import React, { Component } from 'react';
import {connect} from 'react-redux';
import CodeMirror from 'codemirror';
import 'codemirror/mode/javascript/javascript.js';
import 'codemirror/mode/xml/xml.js';
import 'codemirror/mode/markdown/markdown.js';
import checkXMLResponseStructure from '../components/commonComponents/XmlParser';
import './Response.css';

class Response extends Component {  
    //using componentDidMount() only one instance on codemirror wouuld be created and preserved
    componentDidMount(){  
         //   debugger;   
            this.responseEditor=CodeMirror.fromTextArea(this.responseValue,{			
                theme: 'eclipse',
                lineNumbers: true			
            });
            this.responseEditor.on('change', value => {	
                this.responseEditor.save();
                this.responseValue=this.responseEditor.getValue();     
                if (checkXMLResponseStructure(this.responseEditor.getValue().trim())) {
                    this.props.valid({ validResponse: true });
                } else {
                   this.props.valid({ validResponse: false });
                }            
                this.props.onResponseChange(this.responseEditor.getValue());				
            });      
            //debugger;
    } 

    getDefaultRes(){
        //console.log('sdfsdf',this.props.defaultRes);
        if(this.props.defaultRes){
           this.responseEditor.setValue(this.props.defaultRes);
        }
    }

    render(){  
       // console.log(this);            
        return (
                <div className="form-group">
                    <label htmlFor="response">Response:</label>
                    <textarea 
                            ref = {(input)=> this.responseValue = input} /*onChange={this.handleChange.bind(this,event)}*/
                            className="form-control"  
                            value={this.getDefaultRes()} 
                            id="response"/>
                </div>
        )
    }
}


function mapStateToProps(state){
    return{
        defaultRes:state.fetchReducer.defaultRes
    }
}

export default connect(mapStateToProps)(Response);

